import type { AnyFn } from 'fonction'

import { _, INVALID_PLACEHOLDER } from '@/constants'
const isPlaceholder = <T extends unknown>(
  val: T
): T extends typeof _ ? true : false =>
  (val === _) as T extends typeof _ ? true : false

const dealPlaceholder = (placeholder: symbol, fn: AnyFn): unknown => {
  if (isPlaceholder(placeholder)) {
    return fn
  } else {
    throw Error(INVALID_PLACEHOLDER)
  }
}
export { dealPlaceholder, isPlaceholder }
