/* eslint-disable @typescript-eslint/no-explicit-any */
import { isSymbol, isUndefined } from 'fonction'

import { NonIdempotentParams } from '@/types'
import { dealPlaceholder } from '@/utils'

/**
 * Divide its second argument from its first argument.
 *
 * @param a - The first input number
 * @param b - The second input number
 * @returns The result of `a / b`

 * @remarks
 * Since division is not idempotent, there are two ways to curry.
 *
 * @example
 * ```ts
 * // Number
 * divide(10, 100) // 0.1
 * ```
 *
 * @example
 * ```ts
 * // Bigint
 * divide(1n, 2n) // 3n
 * ```
 *
 * @example
 * ```ts
 * // First argument curry
 * const reciprocal = divide(1)
 * reciprocal(4) // 0.25
 * ```
 *
 * @example
 * ```ts
 * // Second argument curry
 * import { _ } from 'arithmetic4'
 * const half = divide(_, 2)
 * half(20) // 10
 * ```
 *
 * @public
 */
const divide: NonIdempotentParams = (a: any, b?: any) => {
  if (isUndefined(b)) return (_b: any) => divide(a, _b)
  else if (isSymbol(a)) {
    return dealPlaceholder(a, (_a: any) => divide(_a, b))
  }
  return (a / b) as any
}
export { divide }
