/* eslint-disable @typescript-eslint/no-explicit-any */
import { isUndefined } from 'fonction'

import { Params } from '@/types'

/**
 * Multiplies first argument and second argument.
 *
 * @param a - The first input number
 * @param b - The second input number
 * @returns The result of `a * b`
 *
 * @example
 * ```ts
 * // Basic
 * multiply(2, 3) // 6
 * ```
 *
 * @example
 * ```ts
 * // Bigint
 * multiply(2n, 3n) // 6n
 * ```
 *
 * @example
 * ```ts
 * // Curry
 * const double = multiply(2)
 * double(4) // 8
 * ```
 *
 * @public
 */
const multiply: Params = (a: any, b?: any) => {
  if (isUndefined(b)) return (_b: any) => multiply(_b, a)

  return (a * b) as any
}
export { multiply }
