/* eslint-disable @typescript-eslint/no-explicit-any */
import { isSymbol, isUndefined } from 'fonction'

import { NonIdempotentParams } from '@/types'
import { dealPlaceholder } from '@/utils'

/**
 * Subtracts its second argument from its first argument.
 *
 * @param a - The first input number
 * @param b - The second input number
 * @returns The result of `a - b`
 *
 * @remarks
 * Since subtraction is not idempotent, there are two ways to curry.
 *
 * @example
 * ```ts
 * // Number
 * subtract(2, 1) // 1
 * ```
 *
 * @example
 * ```ts
 * // Bigint
 * subtract(3n, 2n) //1n
 * ```
 *
 * @example
 * ```ts
 * // First argument curry
 * const from5Minus = subtract(5)
 * from5Minus(10) // -5
 * ```
 *
 * @example
 * ```ts
 * // Second argument curry
 * import { _ } from 'arithmetic4'
 * const minus5 = (_, 5)
 * minus5(20) // 15
 * ```
 *
 * @public
 */
const subtract: NonIdempotentParams = (a: any, b?: any) => {
  if (isUndefined(b)) return (_b: any) => subtract(a, _b)
  else if (isSymbol(a)) {
    return dealPlaceholder(a, (_a: any) => subtract(_a, b))
  }

  return (a - b) as any
}

export { subtract }
